// __tests__/integration/progress-update.test.js
import React from 'react';
import { render, fireEvent, waitFor } from '@testing-library/react-native';
import { router } from 'expo-router';
import Dashboard from '../../src/screens/Dashboard';
import { ProgressProvider } from '../../src/contexts/ProgressContext';
import { ThemeProvider } from '../../src/contexts/ThemeContext';
import { CurrentLevelProvider } from '../../src/contexts/CurrentLevelContext';

// Mock useDashboardState pour éviter les boucles infinies
jest.mock('../../src/screens/Dashboard/hooks/useDashboardState', () => ({
  useDashboardState: jest.fn(() => ({
    showLevelProgress: false,
    setShowLevelProgress: jest.fn(),
    openLevelProgressModal: jest.fn(),
    closeLevelProgressModal: jest.fn(),
    refreshing: false,
    activeTab: "home",
    setActiveTab: jest.fn(),
    onRefresh: jest.fn().mockResolvedValue(),
  })),
}));

// Mock de useLastActivity sans activité
jest.mock('../../src/hooks/useLastActivity', () => () => ({
  lastActivity: null,
  isLoading: false,
  reload: jest.fn(),
}));

// Mock de useRealTimeProgress
jest.mock('../../src/hooks/useRealTimeProgress', () => () => ({
  getLevelProgress: jest.fn(() => 0),
  refresh: jest.fn(),
}));

// Mock du ProgressContext avec updateProgress espionnable
const mockUpdateProgress = jest.fn();

jest.mock('../../src/contexts/ProgressContext', () => {
  const React = require('react');
  
  // Mock du provider qui ne fait rien mais rend ses enfants
  const MockProgressProvider = ({ children }) => {
    return React.createElement(React.Fragment, {}, children);
  };

  return {
    ProgressProvider: MockProgressProvider,
    useProgress: () => ({
      progress: { overall: 0, levels: {} },
      isLoading: false,
      updateProgress: mockUpdateProgress,
      getProgressForLevel: () => 0,
    }),
  };
});

describe('Scénario 1: Mise à jour de la progression', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    mockUpdateProgress.mockClear();
    router.push.mockClear();
  });

  it('devrait mettre à jour la progression après avoir terminé un exercice', async () => {
    const { getByTestId } = render(
      <ThemeProvider>
        <ProgressProvider>
          <CurrentLevelProvider>
            <Dashboard />
          </CurrentLevelProvider>
        </ProgressProvider>
      </ThemeProvider>
    );

    // Attendre que le Dashboard soit rendu
    await waitFor(() => {
      expect(getByTestId('level-selection-button')).toBeTruthy();
    }, { timeout: 5000 });

    const startButton = getByTestId('level-selection-button');

    // Naviguer vers la sélection de niveau
    fireEvent.press(startButton);

    await waitFor(() => {
      expect(router.push).toHaveBeenCalledWith('/tabs/levelSelection');
    });

    // Simuler le flux complet d'un exercice terminé
    const level = 'A1';
    const exerciseType = 'vocabulary';
    const score = 80;

    // Simuler l'appel de mise à jour de la progression
    mockUpdateProgress(level, exerciseType, score);

    // Vérifier que la fonction a été appelée correctement
    expect(mockUpdateProgress).toHaveBeenCalledWith(level, exerciseType, 80);
    expect(mockUpdateProgress).toHaveBeenCalledTimes(1);
  });
});